<template>
	<el-dialog :title="title" :visible.sync="dialogVisible" :width="width" :before-close="handleClose" 
	:modal="modal"  :append-to-body="appendToBody" :custom-class="customClass" :fullscreen="fullscreen" :top="top">
		<!--  -->
		<div class="main">
			<!--  -->
			<slot />
			<!--  -->
			<slot name="img"></slot>
			<slot name="form"></slot>
			<slot name="table"></slot>
		</div>
		<span slot="footer" class="dialog-footer">
			<el-button v-if="isShowBtnConfim" type="primary" @click="handleConfim"> </el-button>
			<el-button v-if="isShowBtnCancle" @click="handleCancle"> </el-button>
		</span>
	</el-dialog>
</template>

<script>
export default {
	name: "QDdialog",
	data() {
		return {};
	},
	props: {
		dialogVisible: {
			type: Boolean,
			default: false,
		},
		width: {
			type: String,
			default: "30%",
		},
		title: {
			type: String,
			default: "",
		},
		isShowBtnConfim: {
			type: Boolean,
			default: true,
		},
		isShowBtnCancle: {
			type: Boolean,
			default: true,
		},
		fullscreen:{
			type: Boolean,
			default: false,
		},
		top:{
			type: String,
			default: '15vh',
		},
		modal:{
			type: Boolean,
			default: true,
		},
		appendToBody:{
			type: Boolean,
			default: true,
		},
		customClass:{
			type: String,
			default: '',
		},
	//  Dialog	boolean		false
	// Dialog CSS  margin-top 	string		15vh
	// 	boolean		true
	//  body  false Dialog 	boolean		true
	// Dialog  body  Dialog  true	boolean		false
	//  Dialog  body 	boolean		true
	// Dialog 	string		
	//  modal  Dialog	boolean		true
	//  ESC  Dialog	boolean		true
	// 	boolean		true
	//  Dialog 	function(done)done  Dialog		
	// 	boolean		false

	},
	components: {},
	mounted() { },
	methods: {
		handleClose(done) {
			this.$emit("update:dialogVisible", false);
			// this.$confirm('').then(_ => {
			// 	done();
			// }).catch(_ => {});
		},
		// 
		handleConfim() {
			// this.$emit("update:dialogVisible", false);
			//  
			this.$emit('confim', false);
		},
		// 
		handleCancle() {
			this.$emit("update:dialogVisible", false);
			this.$emit('cancle')
		},
	},
};
</script>

<style lang="scss" scoped>
.box-card {
	width: 100%;
	box-sizing: border-box;
	padding: 20px;
}
</style>
